const logTitle = [{
		title: 'lv',
		dataIndex: 'lv',
		width: 100,
	},
	{
		title: 'time',
		dataIndex: 'time',
		width: 150,
	},
	{
		title: 'host',
		dataIndex: 'host',
		width: 150,
	},
	{
		title: 'file',
		dataIndex: 'file',
		width: 150,
	},
	{
		title: 'line',
		dataIndex: 'line',
		width: 50,
	},
	{
		title: 'tag',
		dataIndex: 'tag',
		width: 100,
	},
	{
		title: 'note',
		dataIndex: 'note',
		width: 150,
	},
	{
		title: 'content',
		dataIndex: 'content',
	}
];

import axios from 'axios';
import VueAxios from 'vue-axios';

export default {
	data() {
		return {
			start: '',
			end: '',
			lv: '',
			tag: '',
			content: '',
			note: '',
			host: '',
			file: '',
			logList: [],
			logTitle,
			loading: false,
		};
	},
	mounted: function() {
		setTimeout(() => {

		}, 100)
	},
	methods: {
		onSelect({
			key
		}) {
			this.lv = key;
		},
		//搜索日志
		selectLog() {
			
			this.loading = true;

			let data = new FormData();

			if (this.lv == "") {
				this.lv = 0;
			}
			if (this.start == "") {
				this.start = 0;
			}
			if (this.end == "") {
				this.end = 0;
			}

			data.append("start", new Date(this.start).getTime());
			data.append("end", new Date(this.end).getTime());
			data.append("lv", this.lv);
			data.append("tag", this.tag);
			data.append("content", this.content);
			data.append("note", this.note);
			data.append("host", this.host);
			data.append("file", this.file);

			this.$postform('/log/getLog', data)
				.then(res => {
					/**
					 * 日志等级lv（int类型）
					 * 1 Emergency: system is unusable 导致系统不可用的事故
					 * 2 Alert: action must be taken immediately 必须立即处理的问题
					 * 3 Critical: critical conditions 需要立即修复的紧急情况
					 * 4 Error: error conditions 运行时出现的错误，不需要立即进行修复
					 * 5 Warning: warning conditions 可能影响系统功能，需要提醒的重要事件
					 * 6 Notice: normal but significant condition 不影响正常功能，但需要注意的消息
					 * 7 Informational: informational messages 系统正常运行情况下的一般信息
					 * 8 Debug: debug-level messages 开发时对系统进行诊断的信息
					 */

					if (res.code == 200) {
						this.logList = res.data;
						for (let i in this.logList) {
							switch (this.logList[i].lv) {
								case 1:
									this.logList[i].lv = "emergency";
									break;

								case 2:
									this.logList[i].lv = "alert";
									break;

								case 3:
									this.logList[i].lv = "critical";
									break;

								case 4:
									this.logList[i].lv = "error";
									break;

								case 5:
									this.logList[i].lv = "warning";
									break;

								case 6:
									this.logList[i].lv = "notice";
									break;

								case 7:
									this.logList[i].lv = "info";
									break;

								case 8:
									this.logList[i].lv = "debug";
									break;
							}
							this.logList[i].time = this.timestampToTime(this.logList[i].time);
						}
						this.loading = false;
					} else {
						this.loading = false;
					}
				})
		},
		//时间戳转日期字符串
		timestampToTime(timestamp) {
			var date = new Date(timestamp); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-';
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
			var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
			var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
			var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
			var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
			return Y + M + D + h + m + s;
		},
	}
}
