<template>
	<div id="bg">
		<a-page-header
			style="background-color: #FFFFFF;border: 1px solid #dfdfdf;-moz-box-shadow:0px 2px 14px #dfdfdf; -webkit-box-shadow:0px 2px 14px #dfdfdf; box-shadow:0px 2px 14px #dfdfdf;"
			title="Ring Log">
			<template slot="extra">
				<a-popover title="User">
					<template slot="content">
						<a-avatar shape="square" :size="64" icon="user" />
						<br>
						user: <a-input v-model="user"/>
						<br>
						pwd: <a-input v-model="pwd" type="password"/>
						<br>
						<br>
						<a-button type="primary" @click="save()" style="width: 100%;">
							Save
						</a-button>
						
						<br>
						<br>
						<a-button type="danger" @click="clean()" style="width: 100%;">
							Clean
						</a-button>
					</template>
					<a-avatar shape="square" icon="user" />
					<a-tag color="#108ee9" style="font-weight: 600;">{{user}}</a-tag>
				</a-popover>
			</template>
		</a-page-header>
		<a-layout-content>
			<router-view></router-view>
		</a-layout-content>
	</div>
</template>

<script>
	export default {
		name: 'app',
		data() {
			return{
				user: '',
				pwd: '',
			}
		},
		mounted:function(){
			this.user = localStorage.getItem('user');
			this.pwd = localStorage.getItem('pwd');
			localStorage.setItem('address', window.location.origin);
		},
		methods:{
			save() {
				localStorage.setItem('user', this.user);
				localStorage.setItem('pwd', this.pwd);
				this.$message.success('Save user info success');
			},
			clean(){
				this.user = '';
				this.pwd = '';
				localStorage.setItem('user', '');
				localStorage.setItem('pwd', '');
				this.$message.success('Clean user info success');
			}
		}
	}
</script>

<style>
	#bg {
		padding: 0px;
		margin: 0px;
		position: absolute;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
		border: hidden;
		background-color: #edf4ff;
	}
</style>
